
import tabGroup from '@/components/group/tab'
import bigAnniu from '@/components/group/biganniu'
import {getCode, register} from '../../../api/user'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

export default {
components: {
tabGroup: tabGroup,
bigAnniu: bigAnniu,
XInput,
Group,
Grid,
GridItem
},
data () {
return {
anniu: '注册',
line: '请选择授权登录方式',
phone: '18437931109',
qpassword: '',
password: '',
verCode: '',
time: 60,
showCountDownBtn: false,
countDownBtn: '60s',
getCodeBtn: '获取动态码',
agreeCheck: false
}
},
props: {},
computed: {},
methods: {
login () {
this.$router.push('/login')
},
orderhome () {
if (this.password !== this.qpassword || !this.agreeCheck) {
return
}
register({
userName: this.phone,
verCode: this.verCode,
password: this.password,
confPassword: this.qpassword
}).then(res => {
console.log(res)
if (res.data.data) {
this.phone = ''
this.verCode = ''
this.password = ''
this.qpassword = ''
this.$router.push('/orderhome')
}
}).catch(e => {

})
},

clear (type) {
switch (type) {
case 1:
this.phone = '';
break;
case 2:
this.password = '';
break;
case 3:
this.qpassword = '';
break;
default:
break;
}
},
getCode () {
getCode({userName: this.phone, codeType: 1}).then(res => {
console.log(res);
this.showCountDownBtn = true;
let timer = setInterval(() => {
this.time --;
if (this.time <= 0) {
this.time = 60;
this.showCountDownBtn = false;
this.getCodeBtn = '重新获取';
clearInterval(timer)
}
this.countDownBtn = this.time + 's';
}, 1000)
}).catch(e => {
console.log(e)
})
}
}
}



